.page {
  box-sizing: border-box;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-family: @arial;
  color: @special-dark-grey;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

@media (min-width: @tablet-width) {
  body {
    min-width: 768px;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    font-family: @arial;
    color: @special-dark-grey;
  }
}
