.form {
  margin: 0 auto;
  font-family: @oswald;
  font-size: 16px;
  text-transform: uppercase;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.form__input[type="number"] {
  appearance: textfield;
}

.form__input::-webkit-outer-spin-button,
.form__input::-webkit-inner-spin-button {
  appearance: none;
}

.form__group {
  border: none;
  padding: 0;
  margin: 0;
}

.form__legend {
  color: @basic-black;
  font-size: 24px;
  line-height: 26px;
  padding: 0;
  margin: 0;
  font-weight: normal;
  text-transform: none;
}

.form input {
  &::placeholder {
    color: @special-dark-grey;
    text-transform: uppercase;
  }
}


.form__group--grid {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 23px;
  padding: 0 20px;
}

.form__grid {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;
  margin-bottom: 15px;

  &:last-child {
    margin-bottom: 0;
  }
}

.form__input {
  flex-grow: 1;
  width: 90px;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 12px;
  padding-right: 10px;
  border: solid 1px @input-border-grey;
  text-transform: uppercase;
  color: @special-dark-grey;

  &:hover {
    border-color: @toogle-border-grey;
  }

  &:focus {
    outline: none;
    border: 2px solid @special-dark-grey;
    padding-top: 11px;
    padding-bottom: 11px;
    padding-left: 11px;
  }

  &:focus:invalid,
  &:not(:placeholder-shown):not(:focus):invalid {
    border: 2px solid @input-invalid;
  }

  &:focus:invalid + .form__icon-email {
    stroke: @input-invalid;
  }

  &:focus:invalid + .form__icon-tel {
    stroke: @input-invalid;
    fill: @input-invalid;
  }

  &:focus + .form__icon-email {
    stroke: @basic_green;
  }

  &:focus + .form__icon-tel {
    stroke: @basic_green;
    fill: @basic_green;
  }
}

.form__label {
  padding-right: 18px;
  align-self: center;
  cursor: pointer;
  display: block;
}

.form__label--name {
  padding-right: 14px;
}

.form__wrapper-cat-data {
  margin-bottom: 20px;
}

.form__group--radio {
  border-top: 1px solid @border-dark-grey;
  border-bottom: 1px solid @border-dark-grey;
  padding-top: 20px;
  margin-bottom: 6px;
  padding-bottom: 16px;
}

.form__label-radio {
  display: block;
  color: @special-grey;
  padding-left: 45px;
  padding-top: 7px;
  padding-bottom: 7px;
}


.form__radio-grid {
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;
}

.form__program {
  margin: 0;
  margin-bottom: 12px;

  &:last-child {
    margin-bottom: 0;
  }
}

.form__input-radio + .form__label-radio {
  position: relative;

  &::before {
    content: "";
    width: 28px;
    height: 28px;
    border: 1px solid @toogle-border-grey;
    border-radius: 50%;
    left: 0;
    bottom: 1px;
    position: absolute;
    display: block;
  }

  &:hover::before {
    border-color: @checkbox-hover;
  }
}

.form__input-radio {
  &:focus + .form__label-radio::before {
    border-color: @checkbox-hover;
  }
}

.form__input-radio:checked + .form__label-radio {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    display: block;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    left: 9px;
    bottom: 9px;
    background-color: @basic_green;
  }
}

.form__group--cat-owner {
  position: relative;
  margin-bottom: 24px;

  &::before {
    content: "";
    position: absolute;
    width: 31%;
    top: 16px;
    right: 0;
    height: 2px;
    background-color: @basic_green;
    display: block;
  }
}

.form__legend--cat-owner {
  margin-bottom: 35px;
  width: 185px;
  padding-right: 15px;
}

.form__owner-data {
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 15px;

  &:last-child {
    margin-bottom: 0;
  }
}

.form__owner-data--email {
  position: relative;
}

.form__icon-email {
  position: absolute;
  display: block;
  right: 15px;
  top: 16px;
  fill: transparent;
  stroke: @special-grey;
}

.form__owner-data--tel {
  position: relative;
  stroke: none;
  fill: @special-grey;
}

.form__icon-tel {
  position: absolute;
  display: block;
  right: 13px;
  top: 12px;
}

.form__input--owner {
  border: 1px solid @toogle-border-grey;
  padding-right: 45px;
  padding-left: 15px;
  padding-top: 12px;

  &:focus {
    padding-left: 14px;
  }

  &:focus:invalid,
  &:not(:placeholder-shown):not(:focus):invalid {
    padding-top: 11px;
    padding-bottom: 11px;
    padding-left: 11px;
  }
}

.form__label--owner {
  align-self: flex-start;
  padding-top: 9px;
  padding-right: 11px;
}

.form__group--textarea {
  position: relative;
  margin-bottom: 23px;

  &::before {
    content: "";
    display: block;
    position: absolute;
    width: 49.4%;
    top: 15px;
    right: 0;
    height: 2px;
    background-color: @basic_green;
  }
}

.form__legend--textarea {
  width: 120px;
  margin-bottom: 8px;
  padding: 0;
  padding-right: 20px;
  padding-bottom: 30px;
}

.form__textarea-comment {
  min-width: 251px;
  max-width: 251px;
  height: 129px;
  padding-left: 14px;
  padding-right: 14px;
  padding-top: 9px;
  padding-bottom: 10px;
  text-transform: uppercase;
  color: @special-dark-grey;
  border: 1px solid @input-border-grey;
  outline: none;

  &:hover {
    border-color: @toogle-border-grey;
  }

  &:focus {
    border: 2px solid @special-dark-grey;
    padding-top: 8px;
    padding-left: 13px;
    padding-right: 13px;
    padding-bottom: 9px;
  }
}

.form__group--checkbox {
  margin-bottom: 32px;
  position: relative;

  &::before {
    content: "";
    display: block;
    position: absolute;
    width: 42%;
    top: 15px;
    right: 0;
    height: 2px;
    background-color: @basic_green;
  }
}

.form__legend--checkbox {
  margin-bottom: 30px;
  width: 143px;
  padding-right: 20px;
}

.form__checkbox-greed {
  margin: 0;
  margin-bottom: 9px;

  &:last-child {
    margin-bottom: 0;
  }
}

.form__label-checkbox {
  display: block;
  padding-left: 46px;
  padding-top: 8px;
  padding-bottom: 8px;
  color: @basic-black;
}

.form__checkbox + .form__label-checkbox {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    display: block;
    width: 28px;
    height: 28px;
    left: 0;
    top: 3px;
    border: 1px solid @toogle-border-grey;
  }

  &:hover::before {
    border-color: @checkbox-hover;
  }
}

.form__checkbox {
  outline: none;

  &:focus + .form__label-checkbox::before {
    border-color: @checkbox-hover;
  }
}

.form__checkbox:checked + .form__label-checkbox {
  position: relative;

  &::after {
    content: "";
    display: block;
    position: absolute;
    width: 18px;
    height: 14px;
    left: 6px;
    bottom: 8px;
    background-image: url("../img/vector.svg");
  }
}

.form__container-small {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 32px;
}

.form__small {
  text-transform: none;
  font-family: @arial;
  color: @basic-black;
  line-height: 16px;
  padding-top: 21px;
  margin-left: 64px;
  font-size: 14px;
}


.form__submit {
  width: 280px;
  border: none;
  height: 40px;
  margin: 0 auto;
  outline: none;
}

.disabled {
  opacity: 30%;
}

