.page-header {
  display: flex;
  flex-direction: column;
}

.page-header__main-nav {
  color: @basic-black;
  font-family: @oswald;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
}


.page-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-bottom: 14px;
  margin-top: 12px;
  border-bottom: 1px solid @border-grey;
}

.page-header__logo {
  display: block;
  width: 190px;
  height: 39px;
  margin-right: 70px;
  outline: none;

  &:hover[href],
  &:focus[href] {
    opacity: 80%;
  }

  &:active[href] {
    opacity: 60%;
  }
}

.page-header__logo-image {
  width: 190px;
  height: 39px;

  &:hover[href] {
    opacity: 80%;
  }

  &:active[href] {
    opacity: 60%;
  }

  &:focus[href] {
    opacity: 60%;
  }
}


.page-header__toggle {
  border: none;
  width: 18px;
  height: 18px;
  margin-top: 12px;
  background-color: @basic-white;
  display: block;
  outline: none;
}

.page-header__toggle--opened {
  position: relative;

  &::before,
  &::after {
    content: "";
    position: absolute;
    display: block;
    width: 24px;
    height: 2px;
    right: 0;
    top: 7px;
    background-color: @basic-black;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.page-header__toggle--closed {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 24px;
    height: 2px;
    right: 0;
    bottom: 16px;
    background-color: @basic-black;
    display: block;
    box-shadow:
      0 8px 0 0 fade(@basic-black, 100%),
      0 16px 0 0 fade(@basic-black, 100%);
  }
}

.page-header__toggle--nojs {
  display: none;
}

@media (min-width: @tablet-width) {
  .page-header {
    display: grid;
    grid-template-columns: 202px min-content;
    grid-column-gap: 66px;
    margin: 0 auto;
    padding: 0 30px;
    padding-top: 25px;
    margin-bottom: 52px;
    width: 708px;
  }

  .page-header__wrapper {
    display: block;
    padding: 0;
    margin: 0;
    border: none;
  }

  .page-header__logo {
    display: block;
    width: 174px;
    height: 51px;
  }

  .page-header__logo-image {
    width: 174px;
    height: 51px;
    margin: 0;
    padding: 0;
  }

  .page-header__main-nav {
    font-size: 18px;
  }

  .page-header__toggle {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .page-header {
    display: grid;
    grid-template-columns: min-content 1fr;
    grid-column-gap: 470px;
    padding: 0 110px;
    margin-top: 50px;
    width: 1220px;
    margin-bottom: 110px;
  }

  .page-header--form {
    margin-bottom: 74px;
  }


  .page_header__wrapper {
    display: block;
    padding: 0;
    margin: 0;
    border: none;
  }

  .page-header__logo-image {
    width: 202px;
    height: 59px;
    margin: 0;
    padding: 0;
  }

  .page-header__logo {
    display: block;
    width: 202px;
    height: 59px;
    margin-top: 3px;
  }

  .page-header__main-nav {
    font-size: 20px;
    line-height: 30px;
  }

  .page-header__toggle {
    display: none;
  }
}
