.toggle-controls {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-left: 44px;
}

.toggle-controls__scale {
  width: 83px;
  height: 13px;
  border: 1px solid @toogle-border-grey;
  border-radius: 6px;
  background-color: @basic-white;
  position: relative;
  margin-left: 18px;
  margin-right: 17px;
}

.toggle-controls__bar {
  display: block;
  width: 35px;
  height: 6px;
  border-radius: 6px;
  border: none;
  background-color: @basic_green;
  position: absolute;
}

.toggle-controls__bar--before {
  top: 3px;
  left: 5px;
}

.toggle-controls__bar--after {
  top: 3px;
  right: 5px;
}

.toggle-controls__it-was-after {
  border: none;
  background-color: transparent;
  font-family: @oswald;
  color: @basic-black;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  cursor: pointer;
  padding: 0;
}

@media (min-width: @tablet-width) {
  .toggle-controls {
    display: grid;
    margin: 0;
    padding: 0;
    grid-template-columns: min-content min-content min-content;
    margin-left: 67px;
  }

  .toggle-controls__scale {
    width: 428px;
    height: 6px;
    border: none;
    border-radius: 6px;
    background-color: @toogle-border-grey;
    position: relative;
    margin-left: 26px;
    margin-right: 20px;
  }

  .toggle-controls__bar {
    display: block;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    position: absolute;
    background-color: @basic-white;
    border: 1px solid @toogle-border-grey;
    box-shadow: 0 1px 3px @toogle-shadow;
    outline: none;

    &:hover {
      box-shadow: 0 2px 4px @toogle-shadow-hover;
    }

    &:focus {
      box-shadow: 0 2px 4px @toogle-shadow-hover;
    }

    &:active {
      box-shadow: 0 3px 5px @toogle-shadow-active;
      border-color: @basic_green;
    }

    &::before {
      content: "";
      display: block;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: @basic_green;
      position: absolute;
      top: 12px;
      left: 12px;
    }
  }

  .toggle-controls__bar--after {
    right: 10px;
    top: -16px;
  }

  .toggle-controls__bar--before {
    left: 10px;
    top: -16px;
  }

  .toggle-controls__it-was-after {
    border-bottom: 1px dotted @toogle-border-grey;
    padding: 0;
    font-size: 20px;
    line-height: 30px;
    outline: none;

    &:hover {
      border-bottom: 1px solid @toogle-border-grey;
    }

    &:focus {
      border-bottom: 1px solid @toogle-border-grey;
    }

    &:active {
      border-bottom: none;
    }
  }
}

@media (min-width: @desktop-width) {
  .toggle-controls {
    display: grid;
    margin: 0;
    padding: 0;
    margin-left: 110px;
    grid-template-columns: min-content min-content min-content;
  }

  .toggle-controls__scale {
    width: 428px;
    height: 6px;
    border: none;
    border-radius: 6px;
    background-color: @toogle-border-grey;
    position: relative;
    margin-left: 26px;
    margin-right: 19px;
  }

  .toggle-controls__it-was-after {
    border: none;
    border-bottom: 1px dotted @toogle-border-grey;
    padding: 0;
    font-family: @oswald;
    color: @basic-black;
    text-transform: uppercase;
    font-size: 20px;
    line-height: 30px;
  }
}
